<template>
	<view class="content">
		<view class='headers'></view>
		<view class='containers'>
			<u-swiper :list="base_lsit" :height='450' :interval='4000'></u-swiper>
		</view>
		<view class="contentCons">
				<view class="text_area flex-row">
					<view class="text_li">
						项目位置
						<span class='icon'>
							 <icon type="value" size="26"/>
						</span>
					</view>
				<!-- 	<view class="text_li">
						直播看房
						<span class='icon'></span>
					</view> -->
					<view class="text_li" @click.stop="openinfo('/pages/index/details')">
						项目详情
					</view>
				</view>
				<view class='content_activity' v-if='activity'>
					<view class='activity_title flex-row'>
						<span>推荐活动</span>
						<button size='mini' type="default" @click.stop="openinfo('/pages/index/list?pd='+activity.pd)">更多</button>
					</view>
					<view class="activity_content flex-row start" @click.stop="openinfo('/pages/index/activity?id='+activity.id)">
						<image class='activity_img' :src='activity.logo' :lazy-load='true'>
						<view class='activity_content_span flex-column' >
							<span>{{activity.title}}</span>
							<span>{{activity.endTime}}</span>
						</view>
					</view>
				</view>
				<view class='content_activity' v-if='journalism'>
					<view class='activity_title flex-row'>
						<span>新闻资讯</span>
						<button size='mini' type="default" @click.stop="openinfo('/pages/index/list')">更多</button>
					</view>
					<view class="activity_content flex-row start" @click.stop="openinfo('/pages/index/journalism?id='+journalism.id)">
						<image class='activity_img' :src='journalism.logo' :lazy-load='true'>
						<view class='activity_content_span flex-column'>
							<span>{{journalism.title}}</span>
							<span>{{journalism.time}}</span>
						</view>
					</view>
				</view>
				<view class='content_activity' v-if='adviser.length>0'>
					<view class='activity_title flex-row'>
						<span>项目顾问</span>
						<button size='mini' type="default" @click="openinfo('/pages/about/about')">更多</button>
					</view>                        
					<view v-for="(item,index) in adviser" :key="index" class="activity_content flex-row start top"   >
						<image mode='aspectFill' class='activity_portrait' :src='item.logo' :lazy-load='true'>
						<view class='activity_content_span2 flex-column' >
							<span class='zyName' v-if='item.username'>{{item.username}}</span>
							<span class='zyIntroduce' v-if='item.introduce'>{{item.introduce}}</span>
						</view>
						<button class='phone' size='mini' type="default" @click="makePhoneCall(item.telephone)">
							电话咨询
						</button>
					</view>
				</view>
				<view class='content_activity'>
					<view class='activity_title'>
						<span>八大优势</span>
					</view>
					<view class="activity_content_8d">
						<image class='activity_content_8d_img' src='../../static/8d.jpg' :lazy-load='true'>
					</view>
				</view>
		</view>
	</view>
</template>

<script>
	import {
		AkxhHd
	} from '../../api/akxhhd.js'
	export default {
		data() {
			return {
				base_lsit: [],//轮播图
				activity:{},//活动
				journalism:{},//资讯
				adviser:[],//顾问
				chart:{},//8大优势
			}
		},
		onLoad() {
			this.init();
		},
		methods: {
			init(){
				this.getBase();
				this.AkxhHdlists();
			},
			openinfo(url) {
			    uni.navigateTo({
			     url: url,
			    });
			},
			makePhoneCall(phone){
				uni.makePhoneCall({
				    phoneNumber: phone
				});
			},
			AkxhHdlists(){
				//活动列表
				AkxhHd('/catalyzer/AkxhHd/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						this.activity = result[0];
						this.activity.logo = this.imgURL + result[0].logo;
						this.activity.endTime = result[0].endTime.substring(0,10);
						this.activity.pd = 1;
					}
				})
				//资讯列表
				AkxhHd('/catalyzer/AkxhZx/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						this.journalism = result[0];
						this.journalism.logo = this.imgURL + result[0].logo;
						this.journalism.time = result[0].time.substring(0,10);
					}
				})
				//顾问列表
				AkxhHd('/catalyzer/AkxhGw/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						let arr = [];
						result.forEach((items,index,array)=>{
							if(items.isHome == 1){
								items.logo = this.imgURL + items.logo;
								arr.push(items);
							}
						});
						this.adviser = arr;
					}
				})
			},
			getBase() {
				//轮播
				AkxhHd('/catalyzer/AkxhLbt/list','GET').then(res => {
					if (res.data.code == 200) {
						let result = res.data.result;
						let arr = [];
						result.forEach((items,index,array)=>{
							items.image = this.imgURL + items.img;
							arr.push(items);
						});
						this.base_lsit = arr;
					}
				})
			},
			clickItem(data) {
				console.log(data)
			},
			back() {
				uni.redirectTo({
					url: '/pages/index/index',
				})
			}
		}
	}
</script>

<style>
	.headers{
		height: 5vh;
		background: #1c2a39;
		background-image: radial-gradient(left, circle cover, #090e14 0%, #284159 100%);
		/* background-image: radial-gradient(top, circle cover, #090e14 0%, #284159 80%); */
		width: 100vw;
	}
	.containers{
		width: 100vw;
		background-color: #1c2a39;
	}
	/deep/ .header{
		display: none !important;
	}
	/deep/ .wrap .content{
		padding: 0;
	}
	.contentCons{
		background-color: #1c2a39;
		width:100vw;
		color:#fff;
		border-top-left-radius: 25px;
		border-top-right-radius: 25px;
		position: absolute;
		top:230px
	}
	.content_activity{
		width: 95vw;
		background-color: #14212d;
		margin: 15px 2.5vw;
	}
	.content_activity .activity_title span{
		font-size: 18px;
		color:#fff;
		padding: 5px 15px;
		font-weight: 600;
		width: 70%;
	}
	.content_activity .activity_title button{
		font-size:11px;
		line-height: 20px;
		margin: 5px;
		background-color: #304964;
		color:#f0f0f0;
	}
	.content_activity .activity_content{
		width: 100%;
		border: #2C405A solid 1px;
		height: 75px;
	}
	.content_activity .activity_content .activity_img{
		width: 30%;
		height: 75px;
	}
	.content_activity .activity_content .activity_content_span{
		width: 60%;
	}
	.content_activity .activity_content .activity_content_span span{
		padding: 8px;
	}
	.activity_content_8d_img{
		width: 100%;
		height: 880px;
	}
	.content_activity .activity_content .activity_content_span2{
		width: 35%;
		margin: 5%;
	}
	.content_activity .activity_content .activity_portrait{
		width: 65px;
		height: 65px;
		border-radius: 50%;
		margin-left: 5vw;
		border: #e4cea5 solid 3px;
	}
	.content_activity .activity_content .zyName{
		color:#e4cea5;
	}
	.content_activity .activity_content .zyIntroduce{
		color:#d7dee4;
		font-size: 12px;
	}
	.content_activity .activity_content .phone{
		margin:6% 2%;
		color:#b5a483;
		width: 28%;
	}
	.content {
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.logo {
		height: 300rpx;
		width: 700rpx;
		margin-top: 200rpx;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50rpx;
	}

	.text_area {
		width: 90vw;
		margin: 0 5vw;
	}
	.text_area .text_li{
		width: 45vw;
		height: 50px;
		margin: 5px;
		text-align: center;
		line-height: 50px;
		border: #1c2a39 solid 1px;
		border-radius: 8px;
		background: #1c2a39;
		background-image: linear-gradient(#151f2b 0%,#1c2939 30%,#375070 60%,#455570 100%);
		color:#e4cea5;
		font-size: 15px;
	}
</style>
